a {
  text-decoration: none;
  color: #E0ECFF;
}
.card-list {
  cursor: pointer;
}
.card-list a {
  text-decoration: none;
  background: url('../../assets/img/pool-card-background.svg');
}
.card-list .card-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  border-bottom: 1px solid #203B60;
  padding: 0 24px;
}
.card-list .card-header .pool-network-type {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card-list .card-header .pool-network-type .pool-type-network-text {
  margin-left: 12px;
  font-weight: 600;
}
.card-list .card-header .pool-network-type .pool-type-network-text .pool-type-text {
  color: #FFFFFF;
}
.card-list .card-header .pool-network-type .pool-type-network-text .pool-network-text {
  font-size: 14px;
}
.card-list .card-header .pool-network-type .network-logo img {
  width: 28px;
  height: 28px;
}
.card-list .card-header .base-token-logo {
  height: 48px;
}
.card-list .card-header .base-token-logo .base-token-box-bg {
  width: 48px;
  height: 48px;
  display: inline-block;
  border-radius: 50%;
  background-color: #0b182d;
  position: relative;
}
.card-list .card-header .base-token-logo .base-token-box-bg .basa-token-bg {
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  position: absolute;
  justify-content: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.card-list .card-header .base-token-logo .base-token-box-bg .basa-token-bg img {
  width: 20px;
  height: 20px;
}
.card-list .card-header .base-token-logo .base-token-box-bg:nth-child(n) {
  margin-left: -12px;
}
.card-list .card-header .base-token-logo .base-token-box-bg:first-child {
  margin-left: 0;
}
.card-list .card-header .base-token-logo .lp-base-token-bg {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
}
.card-list .card-header .base-token-logo .lp-base-token-bg .lp-con {
  margin: 0 5px;
}
.card-list .card-header .base-token-logo .lp-base-token-bg .basa-token-bg {
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
}
.card-list .card-header .base-token-logo .WBNB,
.card-list .card-header .base-token-logo .WMATIC,
.card-list .card-header .base-token-logo .amUSDC,
.card-list .card-header .base-token-logo .BUSD,
.card-list .card-header .base-token-logo .USDT,
.card-list .card-header .base-token-logo .CAKE,
.card-list .card-header .base-token-logo .DERI,
.card-list .card-header .base-token-logo .TBUSD,
.card-list .card-header .base-token-logo .TDERI {
  background-size: 20px 20px;
}
.card-list .card-header .base-token-logo .DERI,
.card-list .card-header .base-token-logo .TDERI {
  background-image: url('../../assets/img/logo.svg');
  background-repeat: no-repeat;
  background-position: center;
}
.card-list .card-header .base-token-logo .WBNB {
  background-image: url('../../assets/img/WBNB.svg');
  background-repeat: no-repeat;
  background-position: center;
}
.card-list .card-header .base-token-logo .WMATIC {
  background-image: url('../../assets/img/MATIC.svg');
  background-repeat: no-repeat;
  background-position: center;
}
.card-list .card-header .base-token-logo .amUSDC {
  background-image: url('../../assets/img/amusdc.svg');
  background-repeat: no-repeat;
  background-position: center;
}
.card-list .card-header .base-token-logo .BUSD,
.card-list .card-header .base-token-logo .TBUSD {
  background-image: url('../../assets/img/BUSD.svg');
  background-repeat: no-repeat;
  background-position: center;
}
.card-list .card-header .base-token-logo .USDT {
  background-image: url('../../assets/img/USDT.svg');
  background-repeat: no-repeat;
  background-position: center;
}
.card-list .card-header .base-token-logo .CAKE {
  background-image: url('../../assets/img/CAKE.svg');
  background-repeat: no-repeat;
  background-position: center;
}
.card-list .card-content {
  height: 404px;
  padding: 48px 24px 40px 24px;
}
.card-list .card-content .max-apy {
  height: 67px;
}
.card-list .card-content .max-apy-title,
.card-list .card-content .total-liquidity-title {
  font-size: 14px;
}
.card-list .card-content .max-apy-num,
.card-list .card-content .total-liquidity-num {
  font-size: 36px;
  color: #E0ECFF;
  margin-top: 5px;
  height: 45px;
}
.card-list .card-content .total-pool-liquidity {
  margin-top: 24px;
}
.card-list .card-content .your-deri-epoch {
  font-size: 12px;
  height: 20px;
  margin-top: 48px;
}
.card-list .card-content .your-deri-epoch div {
  width: 80px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px;
  border: 1px solid rgba(224, 236, 255, 0.5);
  border-radius: 100px;
}
.card-list .card-content .stake-button {
  margin-top: 24px;
  border: 1px solid transparent;
}
.card-list .card-content .stake-button .button {
  width: 332px;
  height: 56px;
  border: 0;
  background: rgba(255, 255, 255, 0.1);
  color: #FFFFFF;
  border-radius: 4px;
  font-size: 18px;
  font-weight: 600;
}
.open-list {
  cursor: not-allowed;
}
.card-list:hover {
  background: url('../../assets/img/pool-card-background.svg');
  background-repeat: no-repeat;
  background-size: 102%;
}
.card-list:hover .stake-button {
  width: 332px;
  background-color: rgba(85, 119, 253, 0.1);
  border: 1px solid #CD7A37;
  border-radius: 4px;
}
.card-list:first-child .card-header .base-token-box-bg {
  background-color: #111e34;
}
